import {getLyric} from "@/api/songs.js"
class song{
    constructor({id,vkey,mid,name,singer,album,duration,image,url}){
       this.id=id;
       this.vkey=vkey;
       this.mid=mid;
       this.name=name;
       this.singer=singer;
       this.album=album;
       this.duration=duration;
       this.image=image;
       this.url=url;
       this.lyric=""
    }

}
export default function createSong(musicData,vkey){
    return new song({
        id:musicData.songid,
        vkey:vkey,
        mid:musicData.songmid,
        name:musicData.songname,
        singer:createSing(musicData.singer),
        album:musicData.albumname,
        duration:musicData.interval,
        lyric:"",
        image:`https://y.gtimg.cn/music/photo_new/T002R300x300M000${musicData.albummid}.jpg?max_age=2592000`,
       // url:`http://117.41.241.21/amobile.music.tc.qq.com/C400${musicData.songmid}.m4a?fromtag=66uin=5285&guid=907481888&vkey=${vkey}`
         url:'http://117.41.241.19/amobile.music.tc.qq.com/C400000XLtiO1pYlSu.m4a?guid=5714054048&amp;vkey=44E955DC0C883C9A91B732DD43E6161E398F196CB2DE597270FD52B3D0AE38BCBE6A8A0840DA9193C662503B2DE6044F4952D125A43B5253&amp;uin=5285&amp;fromtag=66%22%3E%3Csource%20src=%22http://117.41.241.19/amobile.music.tc.qq.com/C400000XLtiO1pYlSu.m4a?guid=5714054048&amp;vkey=44E955DC0C883C9A91B732DD43E6161E398F196CB2DE597270FD52B3D0AE38BCBE6A8A0840DA9193C662503B2DE6044F4952D125A43B5253&amp;uin=5285&amp;fromtag=66' //
    })
}

function createSing(singer){
  let arr=[];
  for(let m of singer){
    arr.push(m.name)
  }
  return arr.join("/")
}